
import React, { Component } from 'react';
import Home from './HomeComponent';
import Contact from './ContactComponent';
import Projects from './ProjectsComponent';
import Resume from './ResumeComponent';
import Header from './HeaderComponent';
import Footer from './FooterComponent';
import { Switch, Route, Redirect, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import {  postContact } from '../redux/ActionCreators';
import { actions } from 'react-redux-form';
import { TransitionGroup, CSSTransition } from 'react-transition-group';




const mapStateToProps = state => {
    return {
 
    
    }
  }
const mapDispatchToProps = dispatch => ({
 resetContactForm: () => { dispatch(actions.reset('contact'))},
 postContact: (contact) => dispatch(postContact(contact))

});



class Main extends Component {

  
 
  
  render() {




    
    return (

      <body>
        <div>
        <Header 
          />   
        <TransitionGroup>
          <CSSTransition key={this.props.location.key} classNames="page" timeout={300}>
              <Switch>
          
              <Route path= '/home' component={() =><Home/>} />
              <Route  path='/projects' component={() =><Projects/>}/>
              <Route  path='/resume' component={() =><Resume/>}/>
              <Route  path='/contact' component={() => <Contact resetContactForm={this.props.resetContactForm} postContact={this.props.postContact} />} />
        
            <Redirect from='*' to='/home' />
            </Switch>
          </CSSTransition>
        </TransitionGroup>
        <Footer />
        </div>
      </body>
     
    );
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Main));