import * as ActionTypes from './ActionTypes';
import { firestore} from '../firebase/firebase';

export const postContact = (contact) => (dispatch) => {
        
    return firestore.collection('contact').add(contact)
    .then(response => { console.log('Contact', response); alert('Thank you for your contacting me!'); })
    .catch(error =>  { console.log('Contact', error.message); alert('Your message could not be sended\nError: '+error.message); });
};