import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {  Button, Label, Col, Row } from 'reactstrap';
import { Control, Form, Errors } from 'react-redux-form';


const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => (val) && (val.length >= len);
const isNumber = (val) => !isNaN(Number(val));
const validEmail = (val) => /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);


 export default class Contact extends Component {

    constructor(props) {
        super(props);

        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(values) {
        console.log("Current State is: " + JSON.stringify(values));
        this.props.postContact(values);
        this.props.resetContactForm();
    }
   
    render() {
   
    
        return(
        
                <div className="container ">
                    <div className="contactForm col-12 col-lg-9 m-auto">
                        <div className="row">
                            <div className="col-12 col-sm-6"><h2 className="m-auto" >CONTACT ME</h2>
                                <div>
                                    <p className="pt-4">
                                    I'm trying my best to learn and growth as much 
                                    as i can.
                                   If you´re interested in my profile just leave me a brief message and i´ll answer as soon as i can.
                                    </p>
                                </div>
                            </div>
                            <div className="col-12 col-sm-6  ml-auto">
                                <Form model="contact" onSubmit={(values) => this.handleSubmit(values)}>
                                    <Row className="form-group">
                                        <Col md={6} >
                                            <Control.text model=".name" id="name" name="name"
                                                    placeholder="
                                                    Name"
                                                    className="form-control mb-3 "
                                                    validators={{
                                                        required, minLength: minLength(3), maxLength: maxLength(30)
                                                    }}
                                                    />
                                            <Errors
                                                    className="text-danger"
                                                    model=".name"
                                                    show="touched"
                                                    messages={{
                                                        required: 'Required. ',
                                                        minLength: ' Must be greater than 2 characters ',
                                                        maxLength: ' Must be 30 characters or less '
                                                    }}
                                                />
                                        </Col>
                                        <Col md={6}>
                                            <Control.text model=".telnum" id="telnum" name="telnum"
                                                    placeholder="Phone Number"
                                                    className="form-control "
                                                    validators={{
                                                        required, minLength: minLength(3), maxLength: maxLength(15), isNumber
                                                    }}
                                                    />
                                            <Errors
                                                    className="text-danger"
                                                    model=".telnum"
                                                    show="touched"
                                                    messages={{
                                                        required: 'Required. ',
                                                        minLength:  ' Must be greater than 2 numbers ',
                                                        maxLength: ' Must be 15 numbers or less ',
                                                        isNumber: ' Must be a number '
                                                    }}
                                                />
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Col >
                                            <Control.text model=".email" id="email" name="email"
                                                    placeholder="Email"
                                                    className="form-control"
                                                    validators={{
                                                        required, validEmail
                                                    }}
                                                    />
                                            <Errors
                                                    className="text-danger"
                                                    model=".email"
                                                    show="touched"
                                                    messages={{
                                                        required: ' Required. ',
                                                        validEmail: ' Invalid Email Address '
                                                    }}
                                                />
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Col >
                                            <Control.textarea model=".message" id="message" name="message"
                                                placeholder="Message"
                                                    rows="12"
                                                    className="form-control" />
                                        </Col>
                                        </Row>
                                    <Row className="form-group ">
                                        <div className="ml-auto">
                                            <Col >
                                                <Button  type="submit" color="light">
                                                    Send Message
                                                    </Button>
                                            </Col>
                                        </div>
                                    </Row>
                                </Form>
                            </div>
                            </div>
                        <div className="row">
                        <div className="btn-group m-auto" role="group">
                        <a role="button" className="btn btn-dark" href="https://github.com/Danvaspri"><i className="fa fa-github"></i> GitHub</a>
                       
                        <a role="button" className="btn btn-dark" href="https://www.linkedin.com/in/daniel-andr%C3%A9s-v%C3%A1squez-prieto-517527182?lipi=urn%3Ali%3Apage%3Ad_flagship3_profile_view_base_contact_details%3B5O4EGToyTCqv70mSuvOx0g%3D%3D"><i className="fa fa-linkedin"></i> LinkedIn</a>
                    
                  
                    </div>
                        </div>
                  
                                    

                        </div>
                </div>
           
     );
    }    
}




